.add-cell {
  position: relative;
  margin: 4px 0;
  opacity: 0;
  transition: opacity 0.3s ease-in 0.05s;
}

.add-cell:hover {
  opacity: 1.0;
}

.force-visible .add-cell{
  opacity: 1.0;
}

.add-cell .divider {
  position: absolute;
  top: 50%;
  bottom: 50%;
  border-bottom: 1px solid gray;
  width: 95%;
  right: 2.5%;
  left: 2.5%;
  z-index: -1;
}

.add-cell .add-buttons {
  display: flex;
  justify-content: center;
}

.add-cell .add-buttons button {
  margin: 0 20px;
}