.iframe-wrapper {
  position: relative;
  height: 100%;
  flex-grow: 1;
}

.iframe-wrapper iframe {
  height: 100%;
  width: 100%;
}

.preview-error {
  position: absolute;
  top: 10px;
  left: 10px;
  color: red;
}

/* .react-draggable-transparent-selection -> when dragging the bar */
/* not to interfere with drag events inside the iframe */
.react-draggable-transparent-selection .iframe-wrapper:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0;
}
